
@import url('https://fonts.googleapis.com/css2?family=Akatab&family=Bebas+Neue&family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Rancho&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body{
    overflow-x: hidden;
    background: #f4f1f1;
    min-height: 100vh;
}


#header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 30px 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1000;
}

#header .logo{
    font-size: 2em;
    color: #094b65;
    font-weight: 700;
    text-decoration: none;
}

#header ul{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
}

#header ul li a{
    text-decoration: none;
    font-size: 1em;
    margin: 0 12px;
    font-weight: 600;
    color: #094b65;
    padding: 10px 15px;
    border-radius: 10px;
    transition: .4s;
}

#header ul li a:hover, #header ul li a.active{
    color: #fff;
    background: #094b65;
}


.home{
    width: 100%;
    height: 100vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home::before{
   content: '';
   position: absolute;
   left: 0;
   bottom: 0;
   width: 100%;
   height: 100px;
   background: linear-gradient(to top, #ffff00, transparent);
   z-index: 10;
}

.home img{
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    pointer-events: none;
}
.home2 img{
  left: 0;
    top: 100;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #faf293;
    pointer-events: none;
}

.home #text{
    position: absolute;
    font-size: 10vw;
    color: #094b65;
    text-align: center;
    line-height: 0.7em;
    font-family: 'Rancho', cursive;
    transform: translateY(-100%);
}

.home #text span{
    font-size: 0.20em;
    letter-spacing: 2px;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
}

#btn{
    padding: 8px 30px;
    display: inline-block;
    text-decoration: none;
    background: #fff;
    color: #094b65;
    font-size: 1.2em;
    font-weight: 500;
    letter-spacing: 2px;
    border-radius: 40px;
    transform: translateY(100px);
}

.sea{
    
    position: relative;
    display: flex;
    align-items: center;
    padding: 100px;
    background: #f6f0a8;
}

 .sea h2{
  text-align: center;
    font-size: 3.5em;
    display: inline-block;
    color: #f9f6f6;
 }

.sea p{
    font-size: 1em;
    color: #fff;
}
#rocks{
  width: 90%;
}



.words {
  list-style: none;
  text-align: center;
  margin: 0;
  padding: 0;
  color: aliceblue;
}
.letter-effect{
    white-space: pre-wrap;
    filter: blur(calc(var(--p) * 10px));
    opacity: calc(1 - var(--p));

}
.containerrr {
    height: 200vh;
  }
  
  .moving-heading {
    font-size: 3rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.5s ease;
  }
@media screen and (max-width: 600px){
  .home{
    width: 100%;
    height: 100vh;
    
  }
}
@media (max-width: 768px) {
  #header {
      padding: 15px 30px;
  }

  #header ul {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 10px;
  }

  #header ul li a {
      margin: 5px 0;
      padding: 8px 10px;
      border-radius: 5px;
      font-size: 0.9em;
  }

  .home #text {
      font-size: 15vw;
  }

  .home2 img {
      top: 50%;
      transform: translate(0, -50%);
  }

  .sea {
      padding: 30px;
  }
}


@media (min-width: 769px) {
  #header ul {
      align-items: center;
      margin-top: 0;
  }

  .home #text {
      font-size: 10vw;
  }
}


@media (min-width: 1200px) {
  #header {
      padding: 30px 100px;
  }

  #header ul {
      justify-content: space-between;
  }
}